header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-container {
  position: absolute;
  top: 0px;
  left: 10%;
  height: 10vh;
  display: flex;
  align-items: center;
  position: absolute;
  z-index: 1;
}

.logo {
  position: absolute;
  bottom: 0;
  padding-bottom: 1vh;
  font-size: 30px;
  text-decoration: none;
  color: #222222;
  font-family: 'Roboto Mono', monospace;
}

.navbar-container {
  position: absolute;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  width: 100%;
}

.navbar-links-container {
  width: 40vw;
  display: flex;
  justify-content: space-around;
  position: absolute;
  bottom: 0px;
}

.nav-item, .logo {
  flex: 1;
  list-style: none;
  transition: all 0.3 ease;
}

.nav-item {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-link:hover {
  margin-top: -5px;
  border-bottom: 5px solid #fff;
  transition: all 0.3s ease-out;
}

.nav-link {
  width: 90%;
  padding-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #222222;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
}

.menu-container {
  display: none;
  filter: invert(1);
}


@media screen and (max-width: 960px) {
  .navbar-links-container {
    display: none;
  }

  .logo-container {
    left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .menu-container {
    display: block;
    position: absolute;
    right: 10vw;
    cursor: pointer;
    z-index: 1;
  }

  .active {
    display: block;
    width: 100%;
    position: absolute;
    background: red;
    top: 60px;
    z-index: 1;
  }

  .nav-item {
    background-color: #222222;
    height: 9vh;
    display: flex;
    align-self: center;
    justify-content: center;
  }

  .nav-link {
    color: #fff;
    height: 100%;
    padding-bottom: 0px;
  }

  .nav-link:hover {
    color: #242424;
    border-radius: 0;
    margin-top: 0px;
    border-bottom: 0px;
    transition: none;
    color: grey;
  }
}